/* ===== GLOBAL SAFETY ===== */
html,
body {
  width: 100%;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}

/* ===== WRAPPER ===== */
.home-wrapper {
  width: 100%;
  overflow-x: hidden;
}

/* ===== MAIN CONTAINER ===== */
.home-container {
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #4b778a, #203a43, #2c5364);
  padding: 60px 20px;
  text-align: center;
  overflow-x: hidden;
  padding-top: 5px;
}

/* ===== ANT ROW FIX ===== */
.home-row {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

/* ===== TEXT SECTION ===== */
.home-text {
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.name-title {
  font-size: 3rem;
  font-weight: 700;
  color: #ffffff;
  line-height: 1.2;
}

.highlight {
  color: #00e0ff;
}

.subtitle {
  margin: 1rem 0 2rem 0;
  font-size: 1.2rem;
  color: #e0e0e0;
}

.role {
  color: #ff9800;
  font-weight: 600;
}

/* ===== BUTTONS ===== */
.btn-group {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  justify-content: center;
  margin-top: 20px;
}

.hire-btn,
.resume-btn,
.projects-btn {
  min-width: 180px;
  max-width: 220px;
  border-radius: 8px;
  font-weight: 600;
  white-space: nowrap;
  flex: 1 1 auto;
}

.hire-btn {
  background: #00e0ff !important;
  color: #000 !important;
  border: none !important;
}

.resume-btn {
  background: #ff9800 !important;
  color: #fff !important;
  border: none !important;
}

.projects-btn {
  background: #4caf50 !important;
  color: #fff !important;
  border: none !important;
}

/* ===== IMAGE SECTION ===== */
.home-image {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.profile-img {
  width: 280px;
  max-width: 100%;
  height: auto;
  border-radius: 50%;
  border: 5px solid #00e0ff;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.4);
  transition: transform 0.4s ease;
}

.profile-img:hover {
  transform: scale(1.05);
}

/* ===== TABLET ===== */
@media (max-width: 1024px) {
  .name-title {
    font-size: 2.5rem;
  }
  .subtitle {
    font-size: 1.1rem;
  }
  .profile-img {
    width: 240px;
  }
}

/* ===== MOBILE ===== */
@media (max-width: 768px) {
  .home-container {
    flex-direction: column-reverse;
    padding: 40px 15px;
  }

  .home-text {
    text-align: center;
  }

  .btn-group {
    flex-direction: column;
    width: 100%;
  }

  .hire-btn,
  .resume-btn,
  .projects-btn {
    width: 100%;
  }

  .profile-img {
    width: 200px;
  }
}

/* ===== SMALL MOBILE ===== */
@media (max-width: 480px) {
  .name-title {
    font-size: 1.9rem;
  }

  .subtitle {
    font-size: 1rem;
  }

  .profile-img {
    width: 170px;
  }
}
