/* GLOBAL FIX */
html,
body {
  width: 100%;
  overflow-x: hidden;
}

/* WRAPPER */
.about-wrapper {
  width: 100%;
  overflow-x: hidden;
}

/* PAGE */
.about-page {
  min-height: 100vh;
  padding: 40px 20px;
  background: #f8fafc;
}

/* HEADER */
.about-header {
  text-align: center;
  margin-bottom: 50px;
}

.about-title {
  font-size: 3rem;
  font-weight: 800;
  color: #1b9ac0;
}

.header-subtitle {
  font-size: 1.2rem;
  color: #475569;
}

.title-underline {
  width: 80px;
  height: 4px;
  background: linear-gradient(135deg, #4facfe, #00f2fe);
  margin: 15px auto;
}

/* LAYOUT */
.about-content {
  max-width: 1200px;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1.2fr;
  gap: 30px;
}

/* PROFILE CARD */
.profile-card {
  background: white;
  border-radius: 20px;
  padding: 30px;
}

.profile-header {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}

.avatar-circle {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  background: linear-gradient(135deg, #667eea, #764ba2);
  display: flex;
  align-items: center;
  justify-content: center;
}

.avatar-initials {
  color: white;
  font-size: 1.8rem;
  font-weight: 800;
}

.profile-name {
  margin-bottom: 5px;
}

.profile-title {
  color: #64748b;
}

.contact-info {
  margin-top: 15px;
}

.contact-item {
  display: flex;
  gap: 10px;
  color: #475569;
  margin-bottom: 8px;
}

/* STATS */
.profile-stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin: 25px 0;
  gap: 15px;
}

.stat-item {
  text-align: center;
}

.stat-number {
  font-size: 2rem;
  font-weight: 800;
  color: #667eea;
}

.stat-label {
  color: #64748b;
}

/* BUTTONS */
.action-buttons {
  display: flex;
  gap: 15px;
}

.resume-button {
  background: linear-gradient(135deg, #667eea, #764ba2);
  border: none;
}

.contact-button {
  border: 2px solid #667eea;
  color: #667eea;
}

/* CONTENT */
.content-section {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.experience-card,
.education-card {
  background: white;
  padding: 30px;
  border-radius: 20px;
}

.section-title {
  display: flex;
  align-items: center;
  gap: 10px;
}

/* MODAL */
.resume-modal .ant-modal-content {
  border-radius: 20px;
}

.resume-preview {
  padding: 30px;
}

/* RESPONSIVE */
@media (max-width: 1024px) {
  .about-content {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .profile-header {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .profile-stats {
    grid-template-columns: 1fr;
  }

  .action-buttons {
    flex-direction: column;
  }
}
