/* GLOBAL SAFETY */
html,
body {
  width: 100%;
  overflow-x: hidden;
}

/* PAGE */
.projects-page {
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  padding: 60px 20px;
  font-family: "Poppins", sans-serif;
  overflow-x: hidden;
}

/* HEADER */
.projects-header {
  text-align: center;
  margin-bottom: 60px;
}

.projects-header h1 {
  font-size: 2.8rem;
  font-weight: 700;
  background: linear-gradient(135deg, #667eea, #764ba2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.projects-header p {
  font-size: 1.2rem;
  color: #666;
  margin-top: 10px;
}

/* GRID FIX (ANTD) */
.projects-grid {
  width: 100%;
  margin: 0 auto;
}

/* CARD */
.project-card {
  height: 100%;
  border-radius: 16px;
  overflow: hidden;
  background: #fff;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.1);
  transition: all 0.35s ease;
}

.project-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 12px 28px rgba(0, 0, 0, 0.18);
}

/* IMAGE */
.project-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

/* TEXT */
.project-title {
  font-size: 1.3rem;
  font-weight: 600;
  color: #333;
}

.project-description {
  font-size: 1rem;
  color: #666;
  margin-top: 6px;
  display: block;
}

/* BUTTONS */
.project-buttons {
  display: flex;
  gap: 12px;
  margin-top: 20px;
}

.project-buttons button {
  flex: 1;
  border-radius: 8px;
  font-weight: 500;
  white-space: nowrap;
}

/* RESPONSIVE */
@media (max-width: 992px) {
  .projects-header h1 {
    font-size: 2.3rem;
  }

  .project-image {
    height: 180px;
  }
}

@media (max-width: 768px) {
  .projects-header h1 {
    font-size: 2rem;
  }

  .projects-header p {
    font-size: 1rem;
  }

  .project-buttons {
    flex-direction: column;
  }
}

@media (max-width: 480px) {
  .projects-page {
    padding: 40px 15px;
  }

  .projects-header h1 {
    font-size: 1.8rem;
  }

  .project-image {
    height: 160px;
  }
}
